<template>
  <div class="new-ui new-card" :class="classes">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: 'NewButton',
    props: {
      interactable: Boolean
    },
    data: () => {
      provided: {
        expanded: false
      }
    },
    provide: () => {
      card: this.provided
    },
    computed: {
      classes: () => {
        return {
          "expanded": this.provided.expanded,
          "interactable": this.interactable
        }
      }
    }
  }
</script>

<style lang="scss">
  @import '../../styles/styles.scss';
</style>
